import React from 'react';
import { useState } from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import {
  cartAddItem,
  cartRemoveItem,
} from '../../../../redux/cart/cart.action';
import {
  selectCartItems,
  selectCartItemsCount,
} from '../../../../redux/cart/cart.selector';

import ButtonAddRemoveItem from '../../ButtonAddRemoveItem';
import './styles.css';

const MenuItem = ({
  item,
  cartCount,
  cartList,
  cartAddItem,
  cartRemoveItem,
}) => {
  const { id, img, name, options, info } = item;
  const [ forceUpdateValue, setForceUpdate ] = useState(0);

  function forceUpdate() {
    setForceUpdate(forceUpdateValue => forceUpdateValue + 1);
  }

  const handleQuantity = () => {
    let quantity = 0;
    if (cartCount !== 0) {
      const foundItemInCart = cartList.find((item) => item.id === id);
      if (foundItemInCart) {
        quantity = foundItemInCart.quantity;
      }
    }
    return quantity;
  };

  const OptionList = () => {
    if (options != null) {
      return (
        <div className='test-div'>
          <p>Choose one:</p>
          <select onChange={handleChange} className='select-item-options'>
            {
              Object.entries(options.idsAndNames).map(([id, name]) => {
                if (item.selectedOption + 1 == id) {
                  return <option selected key={id} value={id} className='item-option'>{name}</option>
                } else {
                  return <option key={id} value={id} className='item-option'>{name}</option>
                }
              })
            }
          </select>
        </div>
        )
    } else {
      return null;
    }
  }

  function handleChange(event) {
    item.selectedOption = event.target.value - 1
    item.calories = item.options.calories[item.selectedOption]
    forceUpdate()
  }

  const Calories = () => {
    if (item.calories != null) {
      return (
        <div className='item-calories-div'>
          <p className='item-calories'>
              Calories: {item.calories}
          </p>
        </div>
      )
    } else {
      return null
    }
  }

  return (
    <div className='item'>
      <img src={img} alt='food' />
      <div className='item-head_desc'>
        <p className='head_desc-name'>{name}</p>
        <p className='head_desc-info'>{info}</p>
      </div>
      <OptionList/>
      <Calories/>
      <div className='item-add-button-div'>
        <ButtonAddRemoveItem
          quantity={handleQuantity()}
          handleRemoveItem={() => cartRemoveItem(item)}
          handleAddItem={() => cartAddItem(item)}
        />
      </div>
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  cartCount: selectCartItemsCount,
  cartList: selectCartItems,
});

const mapDispatchToProps = (dispatch) => ({
  cartAddItem: (item) => dispatch(cartAddItem(item)),
  cartRemoveItem: (item) => dispatch(cartRemoveItem(item)),
});

export default connect(mapStateToProps, mapDispatchToProps)(MenuItem);
